.activity-attendancde-list-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  background-color: #f0f0f0;
  /* צבע קרם ורדרד */
  width: 100%;
  /* מאפשר למלא את כל הרוחב של המסך */
  position: relative;
  margin-top: -13vh;
  z-index: 1000;
}

.activity-attendance-table th {
  background-color: #937676;
  color: #ffffff;
  font-weight: bold;
}

.activity-attendance-table-title {
  text-align: left;
  margin-bottom: 5px;
  color: #755f5f;
  font-size: xx-large;
  font-weight: bold;
  width: 90%;
  max-width: 900px;
  /* margin-top: 0vh; */
  /* margin-left: -38vh; */
  position: absolute;
  left: 1vw;
  top: 12vh;
}

.activity-attendance-table th,
.activity-attendance-table td {
  padding: 20px 30px;
  text-align: center;
  border-bottom: 1px solid #dddddd;
}

.location-wrapper-attendance {
  display: flex;
  flex-direction: column;
  /* מציג את העיר מעל הרחוב */
  align-items: center;
  /* ממרכז את התוכן אופקית */
  justify-content: center;
  /* ממרכז את התוכן אנכית */
  text-align: center;
  /* ממרכז את הטקסט */
  margin-right: 13vw;
}

.location-wrapper-attendance .city {
  font-size: 20px;
  margin-bottom: 4px;
  font-weight: bold;
  color: #3f3939;
}

/* סגנון לרחוב */
.location-wrapper-attendance .street {
  font-size: 15px;
  color: #3f3939;
  font-weight: normal;
}

.activity-attendance-table {
  width: 100%;
  max-width: 100%;
  border-collapse: collapse;
  background-color: #f0f0f0;
  border-radius: 8px;
  overflow: hidden;
  margin-top: 18vh;
  
}

.activity-attendance-table td.profile-td {
  text-align: left;
  vertical-align: middle;
  padding: 10px;
}

.activity-attendance-table td.city-td {
  text-align: left;
  vertical-align: middle;
  padding: 20px 19px
}

.activity-attendance-table img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.activity-attendance-table tr:hover {
  background-color: #cfcbcb;
}

.activity-attendance-table tr:last-child td {
  border-bottom: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}


.attendance-table-title {
  text-align: left;
  margin-bottom: 5px;
  color: #755f5f;
  font-size: xx-large;
  font-weight: bold;
  width: 90%;
  max-width: 900px;
  /* margin-top: 0vh; */
  /* margin-left: -38vh; */
  position: absolute;
  left: 4vw;
  top: 7vh;
}


.input-check-attendance input[type="checkbox"]:checked {
  appearance: none;
  /* מבטל את ברירת המחדל של הדפדפן */
  background-color: #937676;
  /* צבע חום */
  border: 2px solid #937676;
  /* מסגרת תואמת */
  cursor: pointer;
  width: 2vw;
  height: 4vh;
  border-radius: 50%;
  /* מסגרת עם פינות מעוגלות (אופציונלי) */
  vertical-align: middle;
  /* יישור התיבה למרכז הטקסט */
  margin-right: 10px;
  /* רווח קטן בין התיבה לטקסט */
  position: relative;
  bottom: 7px;

}

/* עיצוב התיבה שלא נבחרה */
.input-check-attendance input[type="checkbox"] {
  appearance: none;
  /* מבטל את ברירת המחדל */
  background-color: #fff;
  /* רקע לבן */
  border: 2px solid #ccc;
  /* מסגרת אפורה */
  cursor: pointer;
  width: 2vw;
  height: 4vh;
  border-radius: 50%;
  /* מסגרת עם פינות מעוגלות (אופציונלי) */
  vertical-align: middle;
  /* יישור התיבה למרכז הטקסט */
  margin-right: 5px;
  /* רווח קטן בין התיבה לטקסט */
  position: relative;
  bottom: 7px;

}

.input-check-attendance input[type="checkbox"]:checked::after {
  content: '✔';
  /* או השתמשי באייקון אחר */
  color: white;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;


}

.input-check-attendance {
  margin-top: 21px;
}

.submit-attendance-button {
  position: fixed;
  bottom: 27px;
  right: 30px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 6px #705050;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}





.submit-attendance-button:hover {
  transform: scale(1.1);
}

.submit-attendance-button:active {
  transform: scale(0.95);
  /* אפקט לחיצה */
}

.filter-wrapper {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.filter-label {
  font-size: 16px;
  font-weight: bold;
  position: absolute;
  /* מיקום יחסי להורה הקרוב עם position: relative */
  top: 9vh;
  /* מרחק מהחלק העליון */
  right: 7vw;
  /* מרחק מהחלק הימני */
}

.filter-input {
  position: absolute;
  /* מיקום יחסי להורה הקרוב עם position: relative */
  top: 16vh;
  right: 3vw;
  /* מרחק מהחלק הימני */
  padding: 8px;
  /* ריווח פנימי */
  border-radius: 4px;
  /* פינות מעוגלות */
  border: 1px solid #ddd;
  border: none;
  border-bottom: 1px solid #ddd;
  border-radius: 25px;
  font-size: 14px;
  color: #3f3939;
  background-color: #fff;
  box-shadow: 0 0px 6px #705050;
  transition: all 0.3s ease-in-out;
}

.filter-input:focus {
  outline: none;
  /* הסרת גבול חיצוני בעת פוקוס */
  border-color: #937676;
  /* שינוי צבע הגבול לפוקוס */
  background-color: #f9f9f9;
  /* שינוי צבע הרקע כשיש פוקוס */
  box-shadow: 0 4px 6px #705050;
}

.filter-input::placeholder {
  font-size: 13px;
}

