}

.notification-header h1 {
  font-size: 24px;
  margin: 0;
  margin-right: -5vw;
}

/* רשימת הודעות */
.notification-list {
  list-style: none;
  padding: 0;
  margin: 20px 0;
}

.notification-item {
  margin-bottom: 15px;
}

.notification-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 -2px 6px #705050;
  padding: 15px;
  transition: transform 0.2s, box-shadow 0.2s;
}

.notification-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.notification-content {
  font-size: 14px;
  margin-bottom: 15px;
  line-height: 1.5;
}

.notification-actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  margin-bottom: -7px;

}


.empty-notification {
  display: flex;
  font-size: 16px;
  top: 23vh;

  position: relative;
  text-align: center;
  color: #6f4f1f;

  align-items: center;

}

.empty-envelope-icon {
  position: absolute;
  top: -3vh;
  left: 50%;
  transform: translateX(-50%);
}

.empty-notification p {
  margin-top: 2px;
  /* רווח בין האייקון למילים */
  margin-right: 4vw;
  color: #3f3939;
}

.empty-notification svg {
  margin-left: 10px;
  color: #6f4f1f;
}

/* כפתור הודעה חדשה */
.new-notification-button {
  background-color: #937676;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  /* transition: background 0.3s; */
  /* display: flex
; */
  display: flex;
  /* הופך את הכפתור לאלמנט מסוג flex */
  justify-content: center;
  /* מרכז את התוכן אופקית */
  align-items: center;
  /* מרכז את התוכן אנכית */
  text-align: center;
  /* grid-gap: 8px; */
  /* grid-gap: 8px; */
  gap: 8px;
  /* margin-top: 48vh; */
  /* margin-left: 16vw; */
  /* margin-right: 6vw; */
  position: fixed;
  right: 7vw;
  bottom:23vh;
  font-size: 16px;
}

.new-notification-button:hover {
  background-color: #8a6f6f;
  /* חום כהה יותר */
}

.new-notification-button svg {
  color: white;
}

.action-button {
  padding: 8px 12px;
  font-size: 14px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: background 0.3s;
  background-color: #937676;
  width: 6vw;
  color: white;
}
.action-button-reply{
  width: 40px;
  height: 39px;  font-size: 14px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background 0.3s;
  background:none;
  color: white;
  justify-content: center;
    align-items: center;
    display: flex
;
}
.action-button-reply:hover{
  background-color: #d3d3d3;
  /* אפור בהיר */
  transform: scale(1.1);
  /* אנימציה קטנה */
}
.action-button:hover{
  background-color: #8a6f6f;

}

.action-button.send {
  margin-right: 19vw;
  margin-left: 1vw;
}


/* מודאל */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: white;
  padding: 20px;
  border-radius: 14px;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

