}
.textarea {
  width: 100%;
  /* מגדיר שהתיבה תתפוס את כל הרוחב של ההורה */
  max-width: 600px;
  /* מגביל את הרוחב המקסימלי */
  height: 60vh;
  border: none;
  padding: 10px;
  /* רווח פנימי */
  font-size: 18px;
  /* גודל גופן גדול יותר */
  resize: vertical;
  /* אפשרות להגדיל רק את הגובה */
  outline: none;
  /* מסיר את המסגרת הכחולה בריכוז */
  transition: border-color 0.3s ease;
  /* אפקט מעבר למסגרת */
  margin-right: -0.8vw;
  margin-top: 9vh;
  resize: none;
  -webkit-appearance: none;
}
.answer-textarea{
  width: 100%;
  /* מגדיר שהתיבה תתפוס את כל הרוחב של ההורה */
  max-width: 600px;
  /* מגביל את הרוחב המקסימלי */
  height: 60vh;
  border: none;
  padding: 10px;
  /* רווח פנימי */
  font-size: 18px;
  /* גודל גופן גדול יותר */
  resize: vertical;
  /* אפשרות להגדיל רק את הגובה */
  outline: none;
  /* מסיר את המסגרת הכחולה בריכוז */
  transition: border-color 0.3s ease;
  /* אפקט מעבר למסגרת */
  margin-right: -0.8vw;
  margin-top: 4vh;
}

.modal h3 {
  margin-top: -2vh;
  margin-bottom: 0vh;
}

.recipient-options {
  margin-top: -1.6vh;
}
/* עיצוב התיבה הנבחרת */
input[type="checkbox"]:checked {
  appearance: none; /* מבטל את ברירת המחדל של הדפדפן */
  background-color:  #937676; /* צבע חום */
  border: 2px solid  #937676; /* מסגרת תואמת */
  cursor: pointer;
  width: 1.5vw;
  height: 3vh;
  border-radius: 50%; /* מסגרת עם פינות מעוגלות (אופציונלי) */
  vertical-align: middle; /* יישור התיבה למרכז הטקסט */
  margin-right: 10px; /* רווח קטן בין התיבה לטקסט */
}

/* עיצוב התיבה שלא נבחרה */
input[type="checkbox"] {
  appearance: none; /* מבטל את ברירת המחדל */
  background-color: #fff; /* רקע לבן */
  border: 2px solid #ccc; /* מסגרת אפורה */
  cursor: pointer;
  width: 1.5vw;
  height: 3vh;
  border-radius: 50%; /* מסגרת עם פינות מעוגלות (אופציונלי) */
  vertical-align: middle; /* יישור התיבה למרכז הטקסט */
  margin-right: 5px; /* רווח קטן בין התיבה לטקסט */
}
input[type="checkbox"]:checked::after {
  content: '✔'; /* או השתמשי באייקון אחר */
  color: white;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.reply-to{
  background-color:#f0f0f0;
  padding: 5px;
  font-style: oblique;
  font-size: 13px;
  margin-top: 5px;
}
.forward-reason{
  background-color:#f0f0f0;
  padding: 5px;
  font-style: oblique;
  font-size: 13px;
  margin-top: 5px;
}
@media only screen and (min-width: 481px) and (max-width: 768px) {
  .notification-header, .answer-notification-header, .new-notification-header {
    width: 70vw;
    margin-right: -2vw;
  }

  .notification-header h1 {
    font-size: 20px;
    margin-right: -4vw;
  }

  .new-notification-button {
    font-size: 14px;
    right: 5vw;
    bottom: 15vh;
  }

  .modal {
    width: 90%;
    max-width: 400px;
  }

  .textarea, .answer-textarea {
    font-size: 16px;
    margin-top: 5vh;
  }

  .action-button {
    width: 7vw;
    font-size: 14px;
  }

  .recipients-scroll, .answer-recipients-scroll {
    width: 40%;
    left: 20vw;
  }
}

/* התאמות למסכים קטנים מאוד - אייפון */
@media (max-width: 480px) {
  .notification-header, .answer-notification-header, .new-notification-header {
    width: 80vw;
    margin-right: -1vw;
  }

  .notification-header h1 {
    font-size: 18px;
    margin-right: -3vw;
  }

  .new-notification-button {
    font-size: 12px;
    right: 4vw;
    bottom: 10vh;
  }

  .modal {
    width: 90%;
    max-width: 350px;
  }

  .textarea, .answer-textarea {
    font-size: 14px;
    margin-top: 3vh;
  }

  .action-button {
    width: 9vw;
    font-size: 12px;
  }

  .recipients-scroll, .answer-recipients-scroll {
    width: 50%;
    left: 15vw;
  }
}