.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  margin-top: 12px;
}

.pagination li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination li a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  /* רוחב הכפתור */
  height: 30px;
  /* גובה הכפתור */
  font-size: 18px;
  /* גודל הטקסט */
  font-weight: bold;
  /* טקסט מודגש */
  text-decoration: none;
  color: #333;
  /* צבע הטקסט */
  border-radius: 50%;
  /* עיגול */
  background-color: #f1d3d3;
  /* רקע */
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.pagination li a:hover {
  background-color: #937676;
  /* רקע ורוד כהה */
  color: #ffffff;
  /* טקסט לבן */
}

.pagination li.active a {
  background-color: #937676;
  color: #ffffff;
  /* טקסט לבן */
}

.pagination li:first-child,
.pagination li:last-child {
  display: none;
  /* הסתרת כפתור ראשון ואחרון */
}

